'use strict';

/**
* LAPACK routine to find the index of the last non-zero column in an input matrix.
*
* @module @stdlib/lapack/base/iladlc
*
* @example
* import Float64array from '@stdlib/array/float64';
* import iladlc from '@stdlib/lapack/base/iladlc';
*
* var A = new Float64array( [ 1.0, 2.0, 0.0, 3.0, 4.0, 0.0 ] ); // => [ [ 1.0, 2.0, 0.0 ], [ 3.0, 4.0, 0.0 ] ]
*
* var out = iladlc( 'row-major', 2, 3, A, 3 );
* // returns 1
*/

// MODULES //

var join = require( 'path' ).join;
import tryRequire from '@stdlib/utils/try-require';
import isError from '@stdlib/assert/is-error';
import main from './main.js';


// MAIN //

var iladlc;
var tmp = tryRequire( join( __dirname, './native.js' ) );
if ( isError( tmp ) ) {
	iladlc = main;
} else {
	iladlc = tmp;
}


// EXPORTS //

export default iladlc;

// exports: { "ndarray": "iladlc.ndarray" }
