'use strict';

// MODULES //

import base from './base.js';


// MAIN //

/**
* Sets the off-diagonal elements and the diagonal elements of a double-precision floating-point matrix to specified values using alternative indexing semantics.
*
* @param {string} uplo - specifies whether to set the upper or lower triangular/trapezoidal part of matrix `A`
* @param {NonNegativeInteger} M - number of rows in matrix `A`
* @param {NonNegativeInteger} N - number of columns in matrix `A`
* @param {number} alpha - value assigned to off-diagonal elements
* @param {number} beta - value assigned to diagonal elements
* @param {Float64Array} A - input matrix
* @param {integer} strideA1 - stride of the first dimension of `A`
* @param {integer} strideA2 - stride of the second dimension of `A`
* @param {NonNegativeInteger} offsetA - starting index for `A`
* @returns {Float64Array} `A`
*
* @example
* import Float64Array from '@stdlib/array/float64';
*
* var A = new Float64Array( 4 );
*
* dlaset( 'all', 2, 2, 2.0, 1.0, A, 2, 1, 0 );
* // A => <Float64Array>[ 1.0, 2.0, 2.0, 1.0 ]
*
* @example
* import Float64Array from '@stdlib/array/float64';
*
* var A = new Float64Array( 4 );
*
* dlaset( 'upper', 2, 2, 2.0, 1.0, A, 2, 1, 0 );
* // A => <Float64Array>[ 1.0, 2.0, 0.0, 1.0 ]
*
* @example
* import Float64Array from '@stdlib/array/float64';
*
* var A = new Float64Array( 4 );
*
* dlaset( 'lower', 2, 2, 2.0, 1.0, A, 2, 1, 0 );
* // A => <Float64Array>[ 1.0, 0.0, 2.0, 1.0 ]
*/
function dlaset( uplo, M, N, alpha, beta, A, strideA1, strideA2, offsetA ) {
	return base( uplo, M, N, alpha, beta, A, strideA1, strideA2, offsetA );
}


// EXPORTS //

export default dlaset;
